<template>
  <div class="coin-exchange">
    <div
      class="coin-exchange__coin-block"
      v-click-outside="() => hideSelectHandler('send')"
    >
      <CoinExchangeContainer
        :label="t('common.send')"
        :coin="sendCoin?.key"
        :rate="136.23"
        @click="showSelectHandler('send')"
      />
      <SelectCurrency
        v-if="showSelect === 'send'"
        class="coin-exchange__select left"
        @select="onSelectCurrency($event, 'send')"
      />
    </div>

    <div class="coin-exchange__icon">
      <Icon
        name="exchange"
        @click="switchCoins"
      />
    </div>

    <div
      class="coin-exchange__coin-block"
      v-click-outside="() => hideSelectHandler('receive')"
    >
      <CoinExchangeContainer
        :label="t('common.receive')"
        :coin="receiveCoin?.key"
        :rate="9036.23"
        @click="showSelectHandler('receive')"
      />
      <SelectCurrency
        v-if="showSelect === 'receive'"
        class="coin-exchange__select right"
        @select="onSelectCurrency($event, 'receive')"
      />
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue'
import { useI18n } from 'vue-i18n'
import CoinExchangeContainer from '@/components/HomePage/CoinExchangeContainer'
import SelectCurrency from '@/components/HomePage/SelectCurrency'
import Icon from '@/components/Icon/Icon'
import clickOutside from '@/directives/clickOutside'
import { useStore } from 'vuex'

export default {
  name: 'CoinExchange',
  components: { CoinExchangeContainer, Icon, SelectCurrency },
  directives: {
    clickOutside
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()

    const receiveCoin = computed(() => store.getters['exchange/getReceiveCoin'])
    const sendCoin = computed(() => store.getters['exchange/getSendCoin'])
    const showSelect = ref(null)

    const showSelectHandler = (val) => {
      if (showSelect.value === val) {
        hideSelectHandler()
        return
      }
      showSelect.value = val
    }
    const hideSelectHandler = (type) => {
      if (showSelect.value === type || !type) {
        showSelect.value = null
      }
    }
    const onSelectCurrency = (coin, type) => {
      if (type === 'send') {
        store.dispatch('exchange/setSendCoin', coin)
      } else {
        store.dispatch('exchange/setReceiveCoin', coin)
      }
      hideSelectHandler()
    }
    const switchCoins = () => {
      const send = { ...sendCoin.value }
      onSelectCurrency({ ...receiveCoin.value }, 'send')
      onSelectCurrency(send, 'receive')
    }

    return {
      t,
      showSelect,
      receiveCoin,
      sendCoin,
      showSelectHandler,
      onSelectCurrency,
      hideSelectHandler,
      switchCoins
    }
  }
}
</script>

<style lang="scss">
.coin-exchange {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

  &__icon {
    margin: 0 12px;
    cursor: pointer;
  }
  &__coin-block {
    width: 50%;
  }
  &__select {
    position: absolute;
    top: calc(100% + 1px);
    z-index: 2;
    box-shadow: $boxShadowTop;

    &.left {
      left: 0;
    }
    &.right {
      right: 0;
    }
  }
}

</style>
