<template>
  <div class="coin-exchange-container">
    <p class="coin-exchange-container__label">{{ label }}</p>
    <Container
      border
      box-shadow
      class="coin-exchange-container__body"
    >
      <CoinContainer
        :title="coinLabel"
        :subtitle="rateLabel"
        :coin="coin"
      />
    </Container>
  </div>
</template>

<script>
import CoinContainer from '@/components/HomePage/CoinContainer'
import { computed } from 'vue'
import Container from '@/components/Container'

export default {
  name: 'CoinExchangeContainer',
  components: { Container, CoinContainer },
  props: {
    coin: {
      type: String,
      required: true
    },
    rate: {
      type: Number,
      default: 0
    },
    label: {
      type: String,
      default: ''
    }
  },
  setup (props) {
    const coinLabel = computed(() => props.coin?.toUpperCase())
    const rateLabel = computed(() => '$' + props.rate)
    return {
      coinLabel,
      rateLabel
    }
  }
}
</script>

