<template>
  <Container border  box-shadow class="select-currency">
    <div
      v-for="item in currencyList"
      :key="item.key"
      class="select-currency__item"
      :class="item.disabled ? 'disabled' : null"
      @click="selectCurrency(item)"
    >
      <CoinContainer
        :title="item.title"
        :subtitle="item.subtitle"
        :coin="item.key"
        :disabled="item.disabled"
      />
    </div>
  </Container>
</template>

<script>
import currencyListMock from '@/store/mock/coinsMock'
import Container from '@/components/Container'
import CoinContainer from '@/components/HomePage/CoinContainer'

export default {
  name: 'SelectCurrency',
  components: { CoinContainer, Container },
  setup (_, { emit }) {
    const currencyList = currencyListMock

    const selectCurrency = (item) => {
      if (item.disabled) {
        return
      }
      emit('select', item)
    }

    return {
      currencyList,
      selectCurrency
    }
  }
}
</script>

<style lang="scss">
.select-currency {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  grid-row-gap: 8px;
  padding: 14px 24px;
  cursor: pointer;

  &__item {
    padding: 6px 8px;
    width: 100%;
    min-width: 141px;
    border-radius: 6px;

    &.disabled {
      background-color: unset !important;
      cursor: not-allowed;
    }
    &:hover {
      background-color: map-get($colors, "default");
    }
  }
}
</style>
