<template>
  <section class="tools">
    <div
      v-for="(item, i) in tools"
      :key="i"
      class="tools__tool"
      :class="{'tools__tool-hover': !item.component}"
      @click="handleClick(item)"
    >
      <component
        :is="item.component || 'Icon'"
        :name="item.icon"
      ></component>
    </div>
  </section>
</template>

<script>
import { defineAsyncComponent } from 'vue'
import { useRouter } from 'vue-router'
import Icon from '@/components/Icon/Icon'
import mock from '@/components/Tools/mock'

export default {
  name: 'Tools',
  components: {
    Icon,
    Notifications: defineAsyncComponent(() => import('./Notifications'))
  },
  props: {
    tools: {
      type: Array,
      default: () => mock
    }
  },
  setup () {
    const router = useRouter()
    const handleClick = item => {
      if (item.to) {
        router.push(item.to)
      } else if (item.copyUrl) {
        const dummy = document.createElement('input')
        const text = window.location.href
        document.body.appendChild(dummy)
        dummy.value = text
        dummy.select()
        document.execCommand('copy')
        document.body.removeChild(dummy)
      }
    }

    return { handleClick }
  }
}
</script>

<style lang="scss">
.tools {
  position: absolute;
  right: 36px;
  top: 14px;
  display: flex;

  &__tool-hover {
    padding: 11px;
    border-radius: 50%;
    cursor: pointer;

    &:hover {
      background-color: map-get($colors, "default");
    }
  }

}
</style>
