<template>
<section class="coin-form">
  <h6 class="coin-form__title">
    {{ t('common.enterAmount') }}
  </h6>

  <div class="coin-form__body">
    <Input
      class="coin-form__body__input"
      type="number"
      :label="t('homepage.currencyToSend', { currency: sendCoin?.title })"
    >
      <template #append>
        <Icon :name="sendCoin?.key + '-circle'" />
      </template>
    </Input>

    <Input
      class="coin-form__body__input"
      type="number"
      :label="t('homepage.currencyToReceive', { currency: receiveCoin?.title })"
    >
      <template #append>
        <Icon :name="receiveCoin?.key + '-circle'" />
      </template>
    </Input>

    <div class="coin-form__body__info">
      <div class="coin-form__body__info__item">
        <Icon
          class="coin-form__body__info__item-icon"
          name="coins"
        />
        <div class="coin-form__body__info__item-label">
          0.002 BTC
        </div>
      </div>
      <div class="coin-form__body__info__item">
        <Tooltip
          :text="t('coinForm.clockTooltip')"
        >
          <Icon
            class="coin-form__body__info__item-icon"
            name="clock"
          />
        </Tooltip>
        <div class="coin-form__body__info__item-label">
          {{ t('common.minute', 10) }}
        </div>
      </div>
    </div>
  </div>

  <Button
    class="coin-form__action"
    type="primary"
    @click="$emit('submit')"
  >
    {{ t('common.continue') }}
  </Button>
</section>
</template>

<script>
import { useI18n } from 'vue-i18n'
import Button from '@/components/UIKit/Button'
import Input from '@/components/UIKit/Input'
import Icon from '@/components/Icon/Icon'
import Tooltip from '@/components/UIKit/Tooltip'
import { useStore } from 'vuex'
import { computed } from 'vue'

export default {
  name: 'CoinForm',
  components: {
    Button, Input, Icon, Tooltip
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()

    const receiveCoin = computed(() => store.getters['exchange/getReceiveCoin'])
    const sendCoin = computed(() => store.getters['exchange/getSendCoin'])

    return {
      t,
      receiveCoin,
      sendCoin
    }
  }
}
</script>

